export default {
	data() {
		return {
			phone: '',
			password: ''
		};
	},
	methods: {
		send() {
			const {
				phone,
				password
			} = this
			if (phone && password) {
				if (!uni.getStorageSync('users')) {
					uni.showToast({
						title: '您还未注册',
						duration: 1000,
						icon: 'none'
					})
				}
				const users = uni.getStorageSync('users') || []
				users.forEach(item => {
					if (item.phone === phone && item.password === password) {
						uni.setStorageSync('token', 'dsadsajlkjl')
						uni.switchTab({
							url: '/pages/user/user'
						})
					} else {
						uni.showToast({
							title: '账号或密码有误',
							duration: 1000,
							icon: 'none'
						})
					}
				})
			} else {
				uni.showToast({
					title: '请填写账号和密码',
					duration: 1000,
					icon: 'none'
				})
			}
		},
		goRegtister() {
			uni.navigateTo({
				url: '/pages/register/register'
			})
		},
		goMovie(){
			uni.reLaunch({
				url:'/pages/movie/movie'
			})
		},
	},
}
